import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
var n=new Date();
var y=n.getFullYear();
var m=n.getMonth()+1;
var d=n.getDate();
    
export default new Vuex.Store({
  state: {
    entrar:false,
    categoria:'',
    Carrito:0,
    Pertenece:false,
    Admin:{},
    estaCompra:[],
    total:0,
    orden:{
      orderID : '1254656',
      customerID :'514866',
      employeeID : '6',
      orderDate :y+'-'+m+'-'+d,
      requiredDate :y+'-'+m+'-'+(d+1),
      shippedDate : y+'-'+m+'-'+d,
      shipVia :0,
      freight :0,
      shipName :'',
      shipAddress : {
      street : '',
      city :'',
      region :'',
      postalCode :'',
      country : ''
      },
      details:[]
     }
  },
  mutations: {
    AgregarAlCarrito(state,Product){
      state.Pertenece=false;
      console.log(state.estaCompra)
     console.log('Sigo entrando')
      for(var item in state.estaCompra){
        console.log(state.estaCompra[item][0]._id,'Probando')
        if(state.estaCompra[item][0]._id===Product[0]._id){
        console.log('El articulo ya estaba',state.estaCompra[item][1])
        var c=parseInt(state.estaCompra[item][1])
        c+=parseInt(Product[1]);
        state.estaCompra[item][1]=c
        state.Pertenece=true;
        }
      } 
      if(!state.Pertenece){
        console.log([Product[0],Product[1]],'estoy agregando')
        state.estaCompra.push([Product[0],Product[1]])
      }
      else{
        console.log('Todo va bien')
      }
      state.total+=(Product[0].unitPrice*Product[1]); 
      state.Carrito+=parseInt(Product[1]);
      console.log(state.total);  
    },
    AgregarOrden(state,Producto){
        if(state.Pertenece){
          console.log('Producto multiplicado')
          for(var item in state.orden.details){
            console.log(state.orden.details[item],'=',Producto[0].productID)
              if(state.orden.details[item].productID===Producto[0].productID){
                console.log('Estoy en orden')
                var c=parseInt(state.orden.details[item].quantity)
                c+=parseInt(Producto[1]);
                state.orden.details[item].quantity=c
            }
        }
      }  
       else{
         
        state.orden.details.push(
                {
                productID :Producto[0].productID,
                unitPrice : Producto[0].unitPrice,
                quantity : Producto[1],
                discount : 0
               }
                  
       )
      }
      console.log(state.orden)
       
    },
    Aumentar(state,precio){
      state.total+=precio;
      state.Carrito++;
    },
    Disminuir(state,precio){
      state.total-=precio;
      state.Carrito--;
    },
    Modificar(state,inform){
        
          state.orden.shipAddress.street=inform.street;
          state.orden.shipAddress.city=inform.city;
          state.orden.shipAddress.region=inform.Region;
          state.orden.shipAddress.postalCode=inform.PostalCode;
          state.orden.shipAddress.country=inform.Country;
        
    },
    Vaciar(state){
      state.estaCompra=[];
      state.orden=[];
      state.total=0;
      state.Carrito=0
    },
    AdminActual(state,item){
      state.Admin=item;
      state.entrar=true;
    },
    SelecCat(state,cat){
      state.categoria=cat
      console.log(state.categoria)
    }
    
  },
  actions: {
  },
  modules: {
  }
})
